<template>
  <div>
    <div class="image-parent" :style="imageParentStyle">
      <img class="image-img" :src="url" :style="imageStyle" />
    </div>
  </div>
</template>

<script>
export default {
  name: "media-image-view",

  components: {},

  computed: {
    url() {
      return this.modelValue;
    },

    imageStyle() {
      let maxHeight = this.imageHeight;
      let maxWidth = this.imageWidth;
      return { maxHeight, maxWidth };
    },

    imageParentStyle() {
      let minHeight = this.imageHeight;
      let minWidth = this.imageWidth;
      return { minHeight, minWidth };
    },
  },

  data() {
    return {};
  },

  methods: {},

  props: {
    modelValue: String, // the url of image
    imageWidth: String,
    imageHeight: String,
  },
};
</script>

<style scoped>
.image-img {
  display: inline-block;
}

.image-parent {
  display: inline-block;
  /* border-color: rgba(128, 128, 128, 0);
  border-style: solid;
  border-width: 2px; */
}
</style>
