<template>
  <div>
    <div class="searchbox"></div>

    <div class="pager">
      <el-pagination
        v-model:current-page="currentPage"
        v-model:page-size="pageSize"
        :page-sizes="[5, 10, 20, 50, 100, 200, 500]"
        :background="background"
        layout="total, sizes, prev, pager, next"
        :total="theCountItemsTotal"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      />
    </div>

    <div class="item-list">
      <ul class="list">
        <li
          class="item"
          v-for="item in items"
          :key="item.id"
          :title="item.name"
          :style="computeItemStyle(item)"
          @click="handleClickItem(item)"
        >
          <img class="item-image" :src="item.url" />
          <div class="item-text">{{ item.label }}</div>
        </li>
      </ul>
    </div>
  </div>
</template>
  
<script>
export default {
  name: "media-selector",

  computed: {
    items() {
      return this.$store.getters["media/list"];
    },

    theCountItemsTotal() {
      return this.$store.getters["media/total"];
    },
  },

  data() {
    return {
      selectedId: 0,
      selectedItem: {},

      currentPage: 1,
      pageSize: 10,
      background: true,
    };
  },

  methods: {
    handleClickItem(item) {
      this.selectedId = item.id;
      this.selectedItem = item;
      this.$emit("change", item);
    },

    computeItemStyle(item) {
      let hit = item.id == this.selectedId;
      let s = {};
      if (hit) {
        s.borderWidth = "3px";
        s.borderColor = "rgb(61, 139, 255)";
      }
      return s;
    },

    fetch() {
      this.$store.dispatch("lazy/load_mediae");
    },

    handleSizeChange(value) {
      let size = value;
      let index = 0;
      this.$store.commit("media/update", { index, size });
    },

    handleCurrentChange(value) {
      let index = value - 1;
      this.$store.commit("media/update", { index });
    },
  },

  mounted() {
    this.fetch();
  },
};
</script>
  
<style scoped>
.list {
  border-color: rgb(206, 206, 206);
  border-style: solid;
  border-width: 1px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  max-width: 50vw;
  max-height: 50vh;
  overflow: scroll;
}

.item {
  border-color: rgb(206, 206, 206);
  border-style: solid;
  border-width: 1px;
  width: 96px;
  margin: 10px;
  cursor: pointer;
  display: inline-block;
}

.item:hover {
  border-color: gray;
}

.item-image {
  max-width: 96px;
  max-height: 96px;
}

.item-text {
  padding: 8px;
  overflow: hidden;
  display: none;
}
</style>
