<template>
  <div>
    <div
      class="image-parent"
      :style="imageParentStyle"
      title="修改"
      @click="displayDialog = true"
    >
      <img class="image-img" :src="url" :style="imageStyle" />
    </div>

    <MySelectorDialog v-model="displayDialog" @change="handleImageSelected" />
  </div>
</template>

<script>
import MySelectorDialog from "./media-selector-dialog.vue";

export default {
  name: "media-image-select",

  components: { MySelectorDialog },

  computed: {
    url() {
      return this.modelValue;
    },

    imageStyle() {
      let maxHeight = this.imageHeight;
      let maxWidth = this.imageWidth;
      return { maxHeight, maxWidth };
    },

    imageParentStyle() {
      let minHeight = this.imageHeight;
      let minWidth = this.imageWidth;
      return { minHeight, minWidth };
    },
  },

  data() {
    return {
      displayDialog: false,
    };
  },

  methods: {
    handleImageSelected(item) {
      let url = item.url;
      this.$emit("update:modelValue", url);
    },
  },

  props: {
    modelValue: String, // the url of image
    imageWidth: String,
    imageHeight: String,
  },
};
</script>

<style scoped>
.image-img {
  display: inline-block;
}

.image-parent {
  display: inline-block;
  border-color: rgba(128, 128, 128, 0);
  border-style: solid;
  border-width: 2px;
  cursor: pointer;
}

.image-parent:hover {
  border-color: rgb(90, 90, 90);
  background-color: gray;
}
</style>
