<template>
  <el-dialog title="选择图片">
    <MySelector class="" @change="handleSelectionChange" />
    <div>
      <el-button type="primary" @click="onok">确定</el-button>
      <el-button type="default" @click="cancel">取消</el-button>
    </div>
  </el-dialog>
</template>

<script>
import MySelector from "./media-selector.vue";

export default {
  name: "media-selector-dialog",
  components: { MySelector },

  data() {
    return {
      item: {},
    };
  },

  methods: {
    handleSelectionChange(item) {
      if (item == null) {
        return;
      }
      this.item = item;
    },

    onok() {
      let item = this.item;
      let id = item.id;
      if (id < 1) {
        return;
      }
      this.$emit("change", item);
      this.close();
    },

    cancel() {
      this.close();
    },

    close() {
      this.$emit("update:modelValue", false);
    },
  },
};
</script>
